#h-menu
{
    position: fixed;
    z-index: 9999; 
    top: 10px;
    right: 10px;

    display: none;

    width: 45px;
    height: 45px;

    border: 1px solid #00a33d;
    border-radius: 5px;
    background: #fff;
}

#h-menu .h-menu-line1,
#h-menu .h-menu-line2,
#h-menu .h-menu-line3
{
    width: 25px; 
    height: 2px;

    background-color: #00a33d;
}

#h-menu .h-menu-line1
{
    position: absolute;
    top: 14px;
    left: 10px;
}

#h-menu .h-menu-line2
{
    position: absolute;
    top: 22px;
    left: 10px;
}

#h-menu .h-menu-line3
{
    position: absolute;
    top: 30px;
    left: 10px;
}

#h-menu .h-menu-line1,
