#h-menu .h-menu-line3
{
    -webkit-transition: all .5s ease-out;
            transition: all .5s ease-out;
}

#h-menu .menu-click1
{
    top: 22px;

    -webkit-transform: rotate(405deg);
            transform: rotate(405deg);
}

#h-menu .menu-click2
{
    background-color: rgba(255, 255, 255, 0);
}

#h-menu .menu-click3
{
    top: 22px;

    -webkit-transform: rotate(-405deg);
            transform: rotate(-405deg);
}

